export default ({ data, location }) => {
const icon = require(`./${data.projectsJson.cover.source}.png`)
  const image = (
    <div className={styles.image}>
      <img src={icon}  alt={data.projectsJson.cover.alternative} />
    </div>
  );


  return (
    <ProjectPage project={data.projectsJson} location={location}>
      <ProjectIntro project={data.projectsJson} media={image} />
      <ProjectDescription project={data.projectsJson}>
      </ProjectDescription>
      <Wrapper>
        <Macbook>
          {/* <Carousel images={images.desktop} /> */}
        </Macbook>
      </Wrapper>
    </ProjectPage>
  );
};


export const query = graphql`
query ProjectPageQuery($slug: String!) {
    projectsJson(slug: {eq: $slug}){
        className
        description
        text
        title
        links{
            website
        }
        slug
        theme{
            background
        }
        cover{
            source
            alternative
        }
    } 
}
`

